.recovery_utils {
    display: flex;
    align-items: center;
    margin-bottom: 3rem;
    align-self: flex-start;
    gap: 1rem;
}

.back_btn {
    background: transparent;
    width: 2rem;
    height: 2rem;
    padding: 0;
    border: 0;
    cursor: pointer;
}

.resetPassword-title {
    font-size: 2rem;
    font-weight: 400;
    line-height: 2.5rem;
    color: #FEFFB3;
    text-wrap: nowrap;
}

.resetPassword-paragraph {
    color: #FEFFB3;
    font-weight: 300;
    font-size: 1.5rem;
    letter-spacing: .25rem;
    line-height: 1;
    margin-bottom: 3rem;
    text-wrap: nowrap;
}

.input {
    background: transparent;
    border: 2px solid #FBE0F3;
    outline: none;
    min-height: 4.625rem;
    height: 100%;
    width: 100%;
    font-size: 1.5rem;
    color: #FBE0F3;
    padding: 0 1.5rem 0 1.5rem;
    margin-bottom: 1rem;
}

.input::placeholder {
    color: rgba(251, 224, 243, 0.5) ;
    font-size: 1rem;
    line-height: 1rem;
    font-weight: 400;
    text-align: start;
    border: 0;
    letter-spacing: 4px;
    padding: 1.875rem 0 1.875rem 0;
}

.btn {
    padding: 1rem 6rem 1rem 6rem;
    font-size: 1.5rem;
    font-weight: 200;
    line-height: 1.75rem;
    background: transparent;
    border: 2px solid #FBE0F3;
    color: #FBE0F3;
    cursor: pointer;
    letter-spacing: .25rem;
    display: flex;
    align-items: center;
    border-radius: 10rem;
    margin-top: 2.5rem;
    margin-bottom: 2.5rem;
    gap: 1.5rem;
}

.btn svg {
  width: 1.5rem;
  height: 1.5rem;
}

.recover-wrapper {
    display: flex;
    justify-content: start;
    align-items: center;
    flex-direction: column;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 50vh;
}

.recover-form {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.btn_wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.error {
    color: #EB443C;
    max-height: 0;
    height: 100%;
    overflow: hidden;
    transition: max-height .3s ease-in-out, opacity .3s ease-in-out;
    font-size: 1rem;
    padding: 0;
    margin: 0;
    font-weight: 400;
    letter-spacing: 4px;
    opacity: 0;
    text-align: center;
    line-height: 1.5rem;
    white-space: pre-line;
    margin-bottom: 1rem;
}

.error_border {
    border-color: rgba(201, 42, 21, 0.85);  
}

.visible {
    opacity: 1;
    max-height: 1.5rem;
}

.email_sended, .email_sended span {
    font-size: 1.5rem;
    font-weight: 400;
    line-height: 1;
    color: #FEFFB3;
    width: 35vw;
    text-wrap: nowrap;
}

.email_sended span {
    color: #FBE0F3;
}

.wrapper_header {
  left: 0;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1920px;
  width: 100%;
  padding: 0 4rem 0 3rem;
  position: relative;
}

.wrapper_header button {
  color: black;
  background: none;
  border: 0;
  padding: 0;
  display: inline-block;
  cursor: pointer;
  height: 2rem;
  color: white;
  transition: all 0.15s ease-in-out;
}

.wrapper_header h2 {
  font-size: 2rem;
  font-weight: 400;
  color: #feffb3;
  padding: 1rem 0 1.25rem 0rem;
  line-height: 2.375rem;
  cursor: pointer;
}

@media (max-width: 768px) {
  .wrapper_header {
    padding: 0 1.5rem;
    margin: 2rem 0;
  }
  .wrapper_header h2 {
    position: absolute;
    font-size: 1.25rem;
    line-height: 1;
    padding: 0;
    margin: 0;
    transform: translateX(50%);
    right: 50%;
  }

  .wrapper_header button {
    margin-left: auto;
    width: 2rem;
    height: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .wrapper_header button svg {
    width: 1rem;
    height: 1rem;
  }

  .recover-wrapper {
    position: relative;
    transform: none;
    width: 100%;
    height: auto;
    max-width: 100%;
    top: auto;
    left: auto;
    padding: 0 1rem;
  }

  .resetPassword-paragraph {
    font-size: 1rem;
    text-wrap: wrap;
    margin-bottom: 1rem;
  }

  .pc_br {
    display: none;
  }

  .btn {
    padding: .75rem 1.25rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1rem;
    border: 1px solid #fbe0f3;
    letter-spacing: 2px;
    margin-top: 2rem;
    gap: .5rem;
  }

  .btn svg {
    width: .5rem;
    height: .5rem;
  }

  .back_btn, .back_btn svg {
    width: 1rem;
    height: 1rem;
  }

  .recovery_utils {
    gap: .5rem;
    margin-bottom: 2rem;
  }
  
  .resetPassword-title {
    font-size: 1.5rem;
  }

  .input {
    border: 1px solid #fbe0f3;
    height: 100%;
    max-width: none;
    width: 100%;
    font-size: .75rem;
    color: #fbe0f3;
    padding: 1rem;
    min-height: auto;
  }

  .input::placeholder {
    font-size: .75rem;
    letter-spacing: 2px;
  }

    .email_sended, .email_sended span {
        font-size: 1rem;
        width: 100%;
        text-wrap: wrap;
    }

    .email_sended span {
        display: block;
        margin-bottom: 1rem;
    }

}